import { Component, OnInit, ViewChild } from '@angular/core';
import { AuthenticationService } from 'src/app/services/authentication.service';
import { PopoverController, ActionSheetController, ModalController, IonInfiniteScroll } from '@ionic/angular';
import { MembersMenuComponent } from './members-menu/members-menu.component';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { Utils } from 'src/commons/Utils';
import { User } from 'src/models/User';
import { ErrorMessage, ToastText } from 'src/commons/Constants';
import { PageDataService } from 'src/providers/page-data/page-data.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-members',
  templateUrl: './members.page.html',
  styleUrls: ['./members.page.scss'],
})

//-------------------------------------------------------------------------------------------------------------------------------//

export class MembersPage implements OnInit {

  @ViewChild(IonInfiniteScroll) infiniteScroll: IonInfiniteScroll;

  //-------------------------------------------------------------------------------------------------------------------------------//

  private readonly logHeader = "MembersPage>>";

  //-------------------------------------------------------------------------------------------------------------------------------//

  private users: Array<User> = new Array<User>();
  public userList: Array<User> = new Array<User>();
  private searchTerm: string;
  private page: number = 1;
  private totalPages: number;
  imageConcatString = Utils.generateRandomString();

  //-------------------------------------------------------------------------------------------------------------------------------// 

  constructor(private router: Router,
    public popoverController: PopoverController,
    private api: BackendApiProvider,
    private utils: Utils,
    public actionSheetController: ActionSheetController,
    public modalController: ModalController,
    private pageDataService: PageDataService) {
    const logHeader = this.logHeader + "constructor>>";
    Utils.log(logHeader);
    this.getUsers();

  }




  //-------------------------------------------------------------------------------------------------------------------------------//

  ngOnInit(): void {
    this.pageDataService.listUserEvent.subscribe((data) => {
      this.users.push(data)
    });
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  getUsers() {
    const logHeader = this.logHeader + "getUsers>>";
    Utils.log(logHeader);
    this.api.getUsers(1).then((response: any) => {
      this.imageConcatString = Utils.generateRandomString();
      let users: Array<User> = response.users;
      this.totalPages = response.totalPages
      this.users = users;
      this.filter(this.searchTerm);
    }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));

  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  filterUsers(users: Array<User>): Array<User> {
    let filteredList: Array<User> = new Array<User>();
    for (let user of users) {
      if (this.api.getLoggedUser().getId() != user.getId()) {
        filteredList.push(user);
      }
    }
    return filteredList;
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  filter(evt) {
    const logHeader = this.logHeader + "filter>>";
    Utils.log(logHeader);
    if (Utils.isStringInvalid(evt)) {
      this.userList = this.users;
      return;
    }
    const searchTerm = evt;
    this.searchTerm = searchTerm;
    if (!searchTerm) {
      this.userList = this.users;
      return;
    }
    this.userList = this.users.filter(n => n.getNome().toLowerCase().includes(searchTerm.toLowerCase()) || n.getEmail().toLowerCase().includes(searchTerm.toLowerCase()));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  changeActiveState(userId: number) {
    const logHeader = this.logHeader + "changeActiveState>>";
    Utils.log(logHeader);
    if (Utils.isVarInvalid(userId) || userId <= 0) {
      throw new Error(logHeader + ErrorMessage.INVALID_ID + ">>" + userId);
    }
    let user = this.users.find(n => n.getId() == userId);
    this.api.changeUserEnabled(user);
    user.setAtivo(!user.isAtivo())
    this.filter(this.searchTerm);
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async openMenu(ev: any) {
    const popover = await this.popoverController.create({
      component: MembersMenuComponent,
      event: ev,
      animated: true,
      showBackdrop: true,
      translucent: true,
      cssClass: 'popover_class'
    });
    return await popover.present();
  }


  async DismissClick() {
    await this.popoverController.dismiss();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//



  async abrirActionSheet(user: User) {
    const actionSheet = await this.actionSheetController.create({
      header: 'O que deseja fazer?',
      buttons: [
        {
          text: 'Editar',
          handler: () => {
            this.pageDataService.setData('user-edit', user)
            this.router.navigate(['users'])
          }
        }, {
          text: 'Excluir',
          role: 'destructive',
          handler: () => {
            console.log('Excluir clicado');
          }
        },
        {
          text: 'Cancelar', role: 'cancel',
          handler: () => {
            console.log('Cancel clicado');
          }
        }]
    });
    await actionSheet.present();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  loadData(event) {
    this.page++;
    const infiniteScroll = event.target;
    this.api.getUsers(this.page).then((response: any) => {
      this.imageConcatString = Utils.generateRandomString();
      let users: Array<User> = response.users;
      this.totalPages = response.totalPages;
      this.users = this.filterUsers(this.users.concat(users));
      this.filter(this.searchTerm);
      infiniteScroll.complete();
      infiniteScroll.disabled = this.page >= this.totalPages;
    }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

}